import React from 'react'
import { useEffect, useState } from 'react'
import { Link } from 'react-router-dom';
import Skeleton from 'react-loading-skeleton'
import 'react-loading-skeleton/dist/skeleton.css'
export default function Products() {
    const [data, setData] = useState([]);
    const [filter, setFilter] = useState(data);
    const [loading, setLoading] = useState(false);
    console.log("Load lai");
    let componentMount = true;
    useEffect(() => {
        const getProduct = async () => {
            setLoading(true);
            const response = await fetch("https://fakestoreapi.com/products");
           
            if (componentMount) {
                setData(await response.clone().json());
                console.log(data)
                setFilter(await response.json());
                setLoading(false);
            }
            return () => {
                componentMount = false;
            }

        }
        getProduct();
       
    }, [])
    const Loading = () => {
        return (
            <>
                {/* <img src="https://cutewallpaper.org/24/loading-gif-png/loadinggifpng5-superior-lawn-care.png" 
                    style={{width: '100px'}}
                    alt="load" 
                /> */}
                <div className="col-md-3">
                    <Skeleton height={350} />
                </div>
                <div className="col-md-3">
                    <Skeleton height={350} />
                </div>
                <div className="col-md-3">
                    <Skeleton height={350} />
                </div>
                <div className="col-md-3">
                    <Skeleton height={350} />
                </div>
            </>
        )
    }
    const ShowProduct = () => {
        return (
            <>
                <div className="buttons text-center mb-5">
                    <button className="btn btn-outline-dark me-2 mb-3">
                        All
                    </button>
                    <button className="btn btn-outline-dark me-2 mb-3">
                        Men's Clothing
                    </button>
                    <button className="btn btn-outline-dark me-2 mb-3">
                        Jewelery Clothing
                    </button>
                    <button className="btn btn-outline-dark me-2 mb-3">
                        Electronic Clothing
                    </button>

                </div>
                {
                    filter.map((product) => {
                        return ( <Link to={`/products/${product.id}`} className="col-md-3 mb-3" key={product.id}>
                                <div className="card h-100 text-center p-4" >
                                    <img src={product.image} height="250px"  className="card-img-top" alt="..." />
                                    <div className ="card-body">
                                    <h5 className ="card-title">{product.title}</h5>
                                    <p className ="card-text">{product.price}</p>
                                    <a href="#" className ="btn btn-primary">Buy now</a>
                                    </div>
                                </div>
                            </Link>
                        )
                    })
                }
            </>
        )

    }
    return (
        <div className="container my-5 py-5">
            <div className="row">
                {console.log("render")}
                <div className="col-12 mb-5">
                    <h1 className="display-6 fw-bolder text-center">Lastest Products</h1>
                    <hr />
                </div>
                <div className="row justify-content-center">
                    {loading ? <Loading /> : <ShowProduct />}
                </div>
            </div>
        </div>
    )
}
