import React from 'react'
import { useEffect, useState } from 'react'
import { useParams } from 'react-router'
import { Link } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import Skeleton from 'react-loading-skeleton'
import 'react-loading-skeleton/dist/skeleton.css'
import { addCart } from '../features/shop/shopslide';
export default function Product() {
    const {id} = useParams();
    const dispatch = useDispatch();
    const [product, setProduct] = useState({});
    const [loading, setLoading] = useState(false);
    useEffect(()=>{
        const getProduct = async () =>{
            setLoading(true);
            const response = await fetch(`https://fakestoreapi.com/products/${id}`);
            setProduct(await response.json());
            setLoading(false);
        }
        getProduct();
    },[id])
   
    const Loading = () => {
        return (
            <>
               <div className="col-md-6">
                   <Skeleton height={400}  />
               </div>
               <div className="col-md-6">
                  <Skeleton height={50} width={300} />
                  <Skeleton height={75} width={600} />
                  <Skeleton height={50} width={150} />
                  <Skeleton height={50} width={300} />
                  <Skeleton height={150} width={600} />
               </div>
            </>
        )
    }
    const ShowProduct = () =>{
        return (
            <>
                <div className="col-md-6">
                    <img src={product.image} alt="product" style={{height: '400px', width: '400px'}} />
                </div>
                <div className="col-md-6">
                    <h4 className="text-uppercase text-black-50">
                        {product.category}
                    </h4>
                    <h1 className="display-5">
                        {product.title}
                    </h1>
                    <p className="lead">
                        Rating {product.rating && product.rating.rate}
                    </p>
                    <h3 className="display-6 fw-bold my-4">
                        $ {product.price}
                    </h3>
                    <p className="lead">
                        {product.description}
                    </p>
                    <button onClick={() => dispatch(addCart({...product, quantity: 1}))}  className="btn btn-outline-dark">
                        Add to cart
                    </button>
                    <Link to={'/cart'} className="btn btn-dark mx-3">
                        Go to Cart
                    </Link>
                </div>
            </>
        )
    }
    return (
        <div className="container mt-5">
            <div className="row justify-content-center">
            {
                loading ? <Loading /> : <ShowProduct />
            }
            </div>
           
        </div>
    )
}
