import { createSlice } from "@reduxjs/toolkit";
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
const initialState = {
    carts: []
};

const Shopping = createSlice({
    name:"Shopping",
    initialState,
    reducers:{
        addCart: (state, action) =>{
            const foundCart = state.carts.find(cart => cart.id === action.payload.id);
            if(foundCart){
               foundCart.quantity+=1;
               toast.info('ðŸ¦„ Update Success!', {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                });
            }
            else{
                state.carts.push(action.payload);
                toast.info('ðŸ¦„ Add Success!', {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    });
            }
           
        },
        deleteCart: (state, action) =>{
            const foundCart = state.carts.findIndex(cart => cart.id === action.payload.id);
            // if(foundCart){
                state.carts.splice(foundCart, 1);
                toast.info('ðŸ¦„ Delete Success!', {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    });
           
        }

    }   
})
export const {addCart, deleteCart} = Shopping.actions;
export default Shopping.reducer;